@import url('https://fonts.googleapis.com/css2?family=Inter:wght@700&family=Mada:wght@700&family=Poppins:wght@400;700&display=swap');

html {
    scroll-behavior: smooth;
}

.nav {
    display: flex; 
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
    height: 80px;
    align-items: center;
    justify-content: space-around;
}

body {
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
}

* {
    margin: 0;
    padding: 0;
    outline: none;
    text-decoration: none;
}


.nav-li {
    display: flex;
    padding: 0px;
    gap: 40px;
}

.nav-li a {
    text-decoration: none;
    color: black;
    transform: all 0.3s ease;
}

.nav-li a:hover {
    border-bottom: 1px solid black;
}

.nav-li li {
    list-style: none;
}

.nav-buttons {
    display: flex;
    width: 170px;
    height: 48px;
    font-family: 'inter', sans-serif;
    color: #545BE8;
    font-size: 18px;
    align-items: center;
    justify-content: space-around;
    margin-right: 100px;
}

.log-in-button {
    padding: 13px 58px;
    margin-right: 25px;
    cursor: pointer;
    border: 1px solid #545BE8;
    border-radius: 10px;
    flex: 0 0 33%;
    max-width: 33%;
    
    transition: background-color 0.3s ease;
}

.register-button {
    padding: 13px 58px;
    cursor: pointer;
    border: 1px solid #545BE8;
    border-radius: 10px;
    flex: 0 0 33%;
    max-width: 33%;
    display: flex;
    justify-content: center;

    transition: background-color 0.3s ease;
}

.register-button:hover,
.log-in-button:hover {
    background-color: #545BE8;
    color: white;
}

.block-block {
    background-color: #F5F5F5;
    padding: 80px 0px;
}

.block-block .block-holder {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.block-block .block-holder .left .left-title p {
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #545BE8;
}

.block-block .block-holder .left .left-title p span {
    font-weight: 700;
    font-family: 'Inter', sans-serif;
    font-size: 18px;
    line-height: 21px;
    color: #545BE8;
}

.block-block .block-holder .left .left-text h1 {
    color: #141E32;
    font-size: 64px;
}

.block-block .block-holder .left .left-text h1 span {
    color: #545BE8;
}

.block-block .block-holder .left .sub-text p {
    color: #969696;
    font-size: 18px;
    line-height: 27px;
    margin-top: 24px;
}

.buttons {
    display: flex;
    align-items: center;
    margin-top: 53px;

}

.buttons a {
    border: 1px solid #545BE8;
    border-radius: 10px;
    background: #545BE8;
    color: white;
    padding: 13px 48px;
    font-size: 18px;
    font-family: 'Inter', sans-serif;
    font-weight: 700;
    line-height: 21px;
}

.buttons img {
    margin-left: 24px;
    cursor: pointer;
}

.block-purple-block {
    background-color: white;
    padding: 80px 0px;
}

.block-purple-block .block-holder {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.left-subtitle {
    margin-top: 16px;
    font-size: 40px;
    line-height: 60px;
    margin-bottom: 16px;
    color: #141E32;
}

.marg-40 {
    margin-right: 40px;
}

.left-title {
    color: #545BE8;
    font-size: 18px;
    line-height: 21px;
}

.left-suptitle {
    font-size: 18px;
    line-height: 27px;
    color: #969696;
}

.purple-block {
    width: 267px;
    height: 267px;
    border-radius: 10px;
    background-color: #545BE8;
}

.purple-block img {
    margin-top: 54px;
    margin-left: 30px;
    border: 10px solid white;
    border-radius: 50%;
    height: 32px;
    width: 32px;
    background-color: white;
}

.title {
    color: white;
    font-family: 'Inter', sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    margin-top: 8px;
    margin-left: 16px;
}

.description {
    color: #DDDDDD;
    font-size: 14px;
    line-height: 21px;
    margin-top: 16px;
    margin-left: 17px;
}

.purple-block-container {
    display: flex;
    position: relative;
}

.left-btn {
    cursor: pointer;
}

.right-btn {
    cursor: pointer;
}

.third-block .block-holder {
    background: linear-gradient(180deg, #545BE8 0%, #272DB0 100%);
    margin-top: 80px;
    padding-top: 33px;
    padding-bottom: 34px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.selected-course {
    font-size: 18px;
    color: #F0C932;
    line-height: 21px;
}

.third-block .block-holder .right h3 {
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    color: white;
    margin-top: 16px;
}

.right-lorem-text {
    color: #DDDDDD;
    font-size: 18px;
    line-height: 27px;
    margin-top: 15px;
}

.stats-block {
    display: flex;
    margin-top: 40px;
}

.stats {
    text-align: center;
    margin-right: 110px;
}

.stats-p {
    color: #DDDDDD;
    font-size: 18px;
    line-height: 21px;
}

.stats-num {
    color: #F0C932;
    line-height: 29px;
    font-size: 24px;
    font-weight: 700;
    font-family: 'Inter', sans-serif;
    margin-bottom: 6px;
}

.fourth-block .block-holder .block {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.block {
    padding: 80px 156px;
}

.right-text {
    margin-left: 80px;
}

.right-text h5 {
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    margin-bottom: 16px;
    color: #545BE8;
}

.right-text h3 {
    font-size: 40px;
    line-height: 60px;
    color: #141E32;
    font-weight: 700;
    margin-bottom: 16px;
}

.right-text p {
    font-size: 18px;
    line-height: 27px;
    color: #969696;
    margin-bottom: 35px;
}

.right-text a {
    border: 1px solid #545BE8;
    border-radius: 10px;
    background: #545BE8;
    color: white;
    padding: 13px 48px;
    font-size: 18px;
    font-family: 'Inter', sans-serif;
    font-weight: 700;
    line-height: 21px;
}

.item-holder {
    box-shadow: 0px 18.05851173400879px 18.05851173400879px 0px #0000000D;
    margin-bottom: 42px;
}

.item-wrapper {
    margin-left: 10px;
    margin-right: 10px;
}

.course-items-block1, .course-items-block2, .under-image-info {
    display: flex;
}

.under-image-info p {
    margin-top: 19px;
    margin-left: 14px;
    font-weight: 700;
    font-size: 21px;
    line-height: 32px;
    color: #141E32;
}

.under-image-info h6 {
    margin-left: 100px;
    margin-top: 26px;
    font-size: 12px;
    line-height: 19px;
    font-weight: 400;
    color: #545BE8;
}

.videos-and-teacher-numbers {
    display: flex;
    color: #969696;
}

.videos-and-teacher-numbers p {
    font-size: 16px;
    line-height: 24px;
    margin-left: 15px;
    margin-top: 6px;
    margin-bottom: 21px;
}

.videos-and-teacher-numbers h6 {
    font-size: 16px;
    line-height: 24px;
    margin-left: 15px;
    margin-top: 6px;
    margin-bottom: 21px;
    font-weight: 400;
}

.img-container {
    position: relative;
    text-align: center;
    color: white;
    z-index: -2;
}

.img-shadow {
    position: absolute;
    width: 328px;
    height: 66.67px;
    bottom: 7px;
    z-index: 1;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
}

.best-course {
    position: absolute;
    background-color: #545BE8;
    padding: 11px 10px 10px 11px;
    top: 22px;
    left: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    z-index: 1;
}

.img-text {
    position: absolute;
    bottom: 13px;
    right: 14px;
    font-weight: 700;
    font-size: 21px;
    line-height: 32px;
    z-index: 1;
}

.comments-title {
    font-weight: 700;
    font-size: 40px;
    line-height: 52px;
    font-family: 'Mada', sans-serif;
    text-align: center;
    margin-bottom: 40px;
    margin-top: 80px;
}

.userInfoHolder {
    display: flex;
}

.comment-item {
    box-shadow: 0px 18.05851173400879px 18.05851173400879px 0px #0000000D;
    padding: 40px 40px;
    z-index: -1;
    margin-bottom: 80px;
    position: relative;
}

.vector-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
}

.comments-container .block-holder {
    display: flex;
    justify-content: space-around;
}

.userInfoHolder .right {
    margin-left: 16px;
}

.comments-userInfo h5 {
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
}

.comments-userInfo h6 {
    line-height: 21px;
    color: #969696;
    font-weight: 400;
    font-size: 14px;
    margin-top: 8px;
}

.five-star {
    margin-top: 24px;
    height: 24px;
}

.comment-from-user {
    color: #969696;
    margin-top: 24px;
    font-size: 18px;
    line-height: 30px;
}

.footer-block .block-holder {
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: #393E46;
}

.footer-logo {
    display: flex;
    align-items: center;
}

.footer-logo h6 {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    line-height: 16px;
    color: white;
    margin-left: 10px;
    font-size: 14px;
}

.footer-block .block-holder .left p {
    color: white;
    margin-top: 24px;
    line-height: 24px;
}

.social-media img {
    padding-right: 16px;
    padding-top: 40px;
}

.footer-block .block-holder {
    padding: 100px;
}

.footer-block .block-holder .right-block {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.footer-block .block-holder .right-block h5 {
    color: white;
    font-size: 18px;
    line-height: 27px;
    margin-bottom: 24px;
}

.footer-block .block-holder .right-block a {
    color: #F4F4F4;
    margin-top: 11px;
}

.responsive-img {
    display: none;
}

@media (max-width: 1350px) {

    .block-purple-block .block-holder {
        display: flex;
        flex-direction: column;
    }

    .block-purple-block .block-holder .left-suptitle {
        margin-bottom: 55px;
    }

    .buttons-block {
        display: none;
    }

    .comments-container .block-holder {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .block-purple-block {
        display: flex;
        align-items: center;
        justify-content: center;
    }

}

@media (max-width: 1200px) {

    .main-image {
        display: none;
    }

    .block-block .block-holder p  {
        text-align: center;
    }

    .block-block .block-holder h1 {
        text-align: center;
    }

    .block-block .block-holder .buttons {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .nav-li {
        display: none;
    }

    .nav {
        justify-content: space-around;
    }

    .fourth-block .block-holder .block {
        display: flex;
        flex-direction: column-reverse;
    }

    .course-list {
        margin: 0;
        text-align: center;
    }

    .course-items-block1, .course-items-block2 {
        margin: 0 auto;
        padding-top: 50px;
    }

    .footer-block .block-holder {
        display: flex;
        flex-direction: column;
    }

    .footer-block .block-holder .left,
    .footer-block .block-holder .footer-logo
    {
        text-align: center;
        justify-content: center;
    }

    .footer-block .block-holder .right-block h5 {
        margin-top: 100px;
    }

    .third-block .block-holder .left img {
        display: none;
    }

    .third-block .block-holder {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .third-block .block-holder p,
    .third-block .block-holder h3,
    .third-block .block-holder .stats {
        text-align: center;
    }

    .third-block .block-holder .stats {
        flex: 0 0 20%;
        max-width: 20%;
    }
    
}

@media (max-width: 1000px) {
    
    .purple-block {
        margin-right: 0px;
    }

    .block-purple-block .block-holder .left {
        text-align: center;
    }

    .block-purple-block .purple-block {
        margin-top: 50px;
    }

    .block-purple-block .purple-block-container {
        display: flex;
        flex-direction: column;
    }

    .course-items-block1, .course-items-block2 {
        display: none;
    }

    .responsive {
        margin-top: 50px;
        width: 300px;
        height: 350px;
    }

    .responsive-img {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
    }

}

@media (max-width: 650px) {

    .stats-block {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .third-block .block-holder .stats {
        max-width: 100%;
        margin-right: 0px;
        margin-top: 25px;
    }

    .nav-buttons, 
    .footer-block .block-holder .left p {
        display: none;
    }

    .block-block .block-holder .left .left-text h1 {
        font-size: 35px;
        margin-top: 25px;
    }

    .block-block .block-holder .buttons {
        display: flex;
        flex-direction: column;
    }

    .block-block .block-holder .left .sub-text p {
        font-size: 14px;
    }

    .block-block .block-holder .left {
        display: block;
        
    }

    .nav {
       justify-content: center;
    }

    .buttons img {
        margin-left: 0;
        margin-top: 25px;
    }
}